        </Breadcrumb>
        <LeftSidebar :open="false">
            <Nav />
        </LeftSidebar>
        <Main
            class="m-flower"
            :withoutRight="true"
            :withoutLeft="true"
            v-loading="loading"
        >
            <div class="m-flower-container">
                <h1 class="m-flower-title">全区服小区花价查询</h1>
                <el-divider class="m-flower-desc"
                    >精准数据·居家种田好帮手</el-divider
                >

                <div class="m-flower-search">
                    <el-row :gutter="20">
                        <el-col :span="7">
                            <el-select
                                class="u-server"
                                v-model="current_server"
                                filterable
                                placeholder="请选择服务器"
                            >
                                <el-option
                                    v-for="item in servers"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="7">
                            <el-select
                                class="u-server"
                                v-model="current_map"
                                filterable
                                placeholder="请选择小区"
                            >
                                <el-option
                                    v-for="item in maps"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="7">
                            <el-select
                                class="u-type"
                                v-model="type"
                                placeholder="请选择花型"
                            >
                                <el-option label="全部" value=""></el-option>
                                <el-option
                                    v-for="item in types"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.key"
                                >
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="3">
                            <el-button
                                class="u-button"
                                type="primary"
                                icon="el-icon-search"
                                :disabled="isGuest"
                                @click="loadData"
                                >查询</el-button
                            >
                        </el-col>
                    </el-row>
                </div>

                <div class="m-flower-all">
                    <div class="m-flower-result" v-if="isTraditional">
                        <el-row :gutter="40" v-if="rank && rank.length">
                            <el-col
                                :span="12"
                                class="u-flower"
                                v-for="(item, i) in rank"
                                :key="i"
                                :class="{ isHidden: item.isHidden }"
                            >
                                <span class="u-title">
                                    <span class="u-name">{{ item.name }}</span>
                                    <span class="u-icons">
                                        <i
                                            class="u-icon"
                                            v-for="(icon, key) in flowers[
                                                item.name
