                                        title="点击快速复制"
                                        v-clipboard:copy="line"
                                        v-clipboard:success="onCopy"
                                        v-clipboard:error="onError"
                                        ><b>{{ line }}</b
                                        >线</span
                                    >
                                    <span class="u-price">价格 : 1.5倍率</span>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                    <div class="m-flower-result" v-else>
                        <el-row :gutter="40" v-if="data && data.length">
                            <el-col
                                :span="12"
                                class="u-flower"
                                v-for="(item, i) in data"
                                :key="i"
                                :class="{ isHidden: item.isHidden }"
                            >
                                <span class="u-title">
                                    <span class="u-name">{{ item.name }}</span>
                                    <span class="u-icons">
                                        <i
                                            class="u-icon"
                                            v-for="(icon, key) in flowers[
                                                item._name
                                            ]"
                                            :key="key"
                                        >
                                            <el-tooltip
                                                effect="dark"
                                                :content="icon.color"
                                                placement="top"
                                            >
                                                <img
                                                    :src="icon.icon | iconURL"
                                                    :alt="icon.color"
                                                />
                                            </el-tooltip>
                                        </i>
                                    </span>
                                </span>
                                <div class="u-desc">
                                    当前最高分线 :
                                    <span
                                        class="u-line"
                                        v-for="(branch, i) in item.branch"
                                        :key="branch + i"
                                        title="点击快速复制"
                                        v-clipboard:copy="branch.number"
                                        v-clipboard:success="onCopy"
                                        v-clipboard:error="onError"
                                        ><b>{{ branch.number }}</b
                                        >线</span
                                    >
                                    <span class="u-price">价格 : 1.5倍率</span>
